.Header{
    padding: 16px;
}

.PostUser
{
    display: flex;
    align-items: center;
}

.PostUser img
{
    object-fit: cover;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    margin-right: 10px;
}

.PostUser span
{
    font-weight: 600;
}

.postContent 
{
    position: relative;
    padding-top: 100%;
    border-top: 1px solid rgba(var(--ce3,239,239,239),1);
    border-bottom: 1px solid rgba(var(--ce3,239,239,239),1);
}

.postImgContainer 
{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
}

.postContent img 
{
    width: 100%;
    object-fit: cover;
    height: 100%;
}

.postActivity 
{
    padding: 0px 16px;
}

.postLikes {
    font-weight: 600;
    padding: 4px 0px;
}

.postActions {
    display: flex;
    align-items: center;
    padding: 6px 16px;
}

.postActions > button {
    margin-right: 10px;
    display: flex;
}

.postComments > div
{
    display: flex;
    line-height: 1.5;
}

.postComments span:first-child {
    display: flex;
    font-weight: 600;
}

.postComments span 
{
    margin-right: 4px;
}

.postTime 
{
    color: #999;
    font-size: 11px;
    padding: 4px 0px;
}

.addComment{
    margin-top: 6px;
    border-bottom: 1px solid #efefef;
    padding: 10px 0;
    display: flex;
}
.addComment input{
    background: none;
    border: none;
    height: 16px;
    outline: none;
    font:inherit;
    padding: 0;
    width: 100%;
}
.addComment ::placeholder { 
    color: #999;
}
.addComment button{
    color:#3897f0;
    font-weight: 600;
    font-size: 16px;
}