.profileHeader{
    display: flex;
    position: relative;
    padding: 16px;
}

.profilePhoto {
    flex-basis: 25%;
    margin-right: 20px;
}

.profilePhoto img {
    object-fit: cover;
    width: 100%;
    border-radius: 50%;
}

.profileId {
    flex-basis: 75%;
}

.profileId> span {
    font-size: 28px;
}

.profileUser {
    padding: 0px 16px;
    margin-bottom: 10px;
}

.profileUser .profileName {
    font-weight: 600;
}

.profileActivity {
    border-top: 1px solid #dbdbdb;
    border-bottom: 1px solid #dbdbdb;
    padding: 10px 0px;
    display: flex;
    list-style: none;
    margin-bottom: 0px;
}
.profileActivity li {
    width: 33.3%;
    color: #999;
    text-align: center;
    font-size: 14px;
}

.profileActivity li span {
    color: black;
    font-weight: 600;
}

ul {
    display: block;
    list-style-type: disc;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 40px;
}

.thumbnails {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 5px;
}

.followBtn{
    background: #3897f0;
    color: #fff;
    padding: 5px 10px;
    border-radius: 5px;
    font-weight: 600;
    margin:10px 0px;
}
.unfollowBtn{
    background:transparent;
    border: 1px solid #dbdbdb;
    color: black;
    padding: 5px 10px;
    border-radius: 5px;
    font-weight: 600;
    margin:10px 0px;
}
/* this will change the cursor to indicate its clickability*/
button{
  cursor:pointer;
}